.book-now-container{position:relative;width:25%}.book-now{position:sticky;background-color:#fff;width:100%;border-radius:8px;border:1px solid #d7dce3;top:0}.book-now__heading{padding:17px 20px;background:#5191fa;font-size:12px;border-radius:5px 5px 0 0}.book-now__heading h1{color:#fff !important;margin:0 !important}.book-now__heading h1 span{color:#fff !important;margin-right:10px}.book-now__section{padding:15px 20px;display:flex;align-items:center}.book-now__date{cursor:pointer;flex-direction:column}.book-now__date .book-now__block{display:flex;justify-content:space-between;width:100%}.book-now__date .book-now__block:after{content:"";font-family:"Font Awesome 5 Pro";display:inline-flex;font-size:25px;font-weight:700;margin-right:10px}.book-now__date .book-now__hidden{visibility:hidden;display:none}.book-now__date .book-now__hidden.active{visibility:visible;display:block}.book-now__block{flex:0 0 60%}.book-now__submit{display:flex;justify-content:center;padding:17px 20px}.book-now__submit button{width:100%;font-size:16px;padding:12px 25px;background:#5191fa;color:#fff;box-shadow:none;border-radius:3px;transition:background .2s,color .2s;display:inline-block;margin-bottom:0;font-weight:400;text-align:center;white-space:nowrap;cursor:pointer;border:1px solid transparent}.book-now__submit button:hover{background:#4977fa;color:#fff}.book-now .book-count{flex:0 0 40%;display:flex;justify-content:space-between;align-items:center}.book-now .book-count input{font-size:20px;border:none;box-shadow:none;width:35px;margin:0;color:#5191fa}.book-now .book-count span{font-size:35px;font-weight:100;cursor:pointer;user-select:none}.book-now .react-calendar{width:100%}