.tour-card{width:352.5px;margin-right:25px;background-color:#fff;color:#212529;padding-bottom:10px;border-radius:15px}.tour-card__thumb{overflow:hidden;margin-bottom:16px;border-radius:10px 10px 0 0;max-height:259px}.tour-card__thumb:hover img{transform:scale(1.1)}.tour-card__link img{width:101%;height:auto;transition:all .3s ease 0s;background-position:center center}.tour-card__location{padding-left:15px;padding-right:15px;font-size:13px}.tour-card__location:before{content:"";font-family:"Font Awesome 5 Pro";display:inline-flex;font-size:18px;font-weight:700;color:#000;margin-right:5px}.tour-card__title{font-size:18px !important;padding-left:15px;padding-right:15px;color:#000;font-weight:700}.tour-card__review{margin-top:15px;justify-content:flex-start;padding:8px 15px 25px}.tour-card__footer{padding-left:15px;padding-right:15px;width:100%;justify-content:space-between}.tour-card__duration{display:flex;align-items:center}.tour-card__duration:before{content:"";font-family:"Font Awesome 5 Pro";display:inline-flex;font-size:20px;color:#000;margin-right:10px}.tour-card__duration span{font-size:15px}.tour-card__price{display:flex;align-items:center}.tour-card__price .tour-card__fr-text{margin-right:5px}.tour-card__price .tour-card__fr-text:before{content:"";font-family:"Font Awesome 5 Pro";display:inline-flex;font-size:15px;margin-right:5px;color:#ffab53;font-weight:700}.tour-card__price .tour-card__price-text{font-size:20px;font-weight:500}